@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  list-style: none;
  font-family: 'Montserrat', sans-serif;

}

.wrapper-random {
	display: flex;
	justify-content: center;
	align-items: center;
	float:left;
	font-family: Montserrat;
	width: 100%;
	height: 80vh;
	z-index: -2;
}

.wrapper{
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%,-50%);

}

.wrapper .search_box{
  width: 600px;
  background: #fff;
  border-radius: 5px;
  height: 65px;
  display: flex;
  padding: 10px;
  box-shadow: 0 8px 6px -10px #b3c6ff;
}

.wrapper .search_box .dropdown-categories{
  width: 200px;
  border-right: 2px solid #dde2f1;
  color: #9fa3b1;
  position: relative;
  cursor: pointer;
  z-index: 0 !important;
}

.wrapper .search_box .dropdown-categories .default_option{
  text-transform: uppercase;
  padding: 13px 15px;
  font-size: 14px;
}

.wrapper .search_box .dropdown-categories ul{
  position: absolute;
  top: 70px;
  left: -10px;
  background: #fff;
  width: 150px;
  border-radius: 5px;
  padding: 20px;
  display: none;
  box-shadow: 8px 8px 6px -10px #b3c6ff;
}

.wrapper .search_box .dropdown-categories ul.active{
  display: block;
}

.wrapper .search_box .dropdown-categories ul li{
  padding-bottom: 20px;
}

.wrapper .search_box .dropdown-categories ul li:last-child{
  padding-bottom: 0;
}

.wrapper .search_box .dropdown-categories ul li:hover{
  color: #6f768d;
}

.wrapper .search_box .dropdown-categories:before{
  content: "";
  position: absolute;
  top: 18px;
  right: 20px;
  border: 8px solid;
  border-color: #5078ef transparent transparent transparent;
}

.wrapper .search_box .search_field{
  width: 400px;
  height: 100%;
  position: relative;
}

.wrapper .search_box .search_field .input{
  width: 100%;
  height: 100%;
  border: 0px;
  font-size: 16px;
  padding-left: 20px;
  padding-right: 38px;
  color: #6f768d;
}

.wrapper .search_box .search_field .fas{
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 22px;
  color: #5078ef;
  cursor: pointer;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #9fa3b1;
}
::-moz-placeholder { /* Firefox 19+ */
 color: #9fa3b1;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #9fa3b1;
}
