#flash-message{position:fixed;top:6.6rem;right:-100%;height:9rem;width:calc(9rem + 20rem);z-index:999;display:-webkit-flex;display:flex;cursor:pointer;border-top-left-radius:0.8rem;border-bottom-left-radius:0.8rem;transition:right 0.6s cubic-bezier(0.36, 0, 0.66, -0.56)}#flash-message .flash-content{width:20rem;padding:1.2rem 1rem;border-left-width:1rem;border-left-style:solid}#flash-message .flash-content .flash-heading{font:700 2rem Archivo;color:#fff;margin-bottom:0.6rem}#flash-message .flash-content .flash-message{font:400 1.2rem Poppins;color:#fff}#flash-message .flash-icon{border-top-left-radius:0.8rem;border-bottom-left-radius:0.8rem;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;width:9rem;height:9rem;background:#fff}#flash-message .flash-icon img{border-radius:50%;padding:1.2rem;width:7rem;height:7rem;object-fit:cover}#flash-message.flash-active{right:0}#flash-message.error-message .flash-icon img{border:1rem solid #ffbfc0;background:#e33d3d}#flash-message.error-message .flash-content{border-left-color:#f37375;background:linear-gradient(45deg, #e33d3d, #f57b7d)}#flash-message.success-message .flash-icon img{border:1rem solid #aff5d0;background:#04d361}#flash-message.success-message .flash-content{border-left-color:#67db9e;background:linear-gradient(45deg, #04d361, #6de0a8)}@media (min-width: 900px){#flash-message{width:32rem}#flash-message .flash-content{width:24rem}}

#semiTransparenDiv {
	width:100%;
	
	/*-Lets Center the Spinner-*/
    position:fixed;
    left:0;
    right:0;
    top:0;
    bottom:0;    
    background-color: rgba(255,255,255,0.7);
    z-index:9999;
    display: none; 
}

@-webkit-keyframes spin {
	from {-webkit-transform:rotate(0deg);}
	to {-webkit-transform:rotate(360deg);}
}

@keyframes spin {
	from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
	to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

#semiTransparenDiv::after {
    content:'';
    display:block;
    position:absolute;
    left:48%;top:40%;
    width:80px;height:80px;
    border-style:solid;
    border: 5px solid transparent;
	border-top-color: #5664D2;
    border-width: 7px;
    border-radius:50%;
    -webkit-animation: spin .8s linear infinite;
    
    /* Lets make it go round */
    animation: spin .8s linear infinite;
}
.wrapper-random {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	float:left;
	font-family: Montserrat;
	width: 100%;
	height: 80vh;
	z-index: -2;
}

.outer {
	position: relative;
	background: #fff;
	height: 350px;
	width: 550px;
	overflow: hidden;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	margin-left: 20px;
	margin-top: 8rem;
	/* z-index: -1; */
}

.image-item {
	position: absolute;
	top: 0px;
	right: -20px;
	z-index: 0;
	-webkit-animation-delay: 0.5s;
	        animation-delay: 0.5s;
}

.content-item {
	-webkit-animation-delay: 0.3s;
	        animation-delay: 0.3s;
	position: absolute;
	left: 20px;
	z-index: 3
	
}

h1 {
	color: #111;
}

.paragraph {
	width: 280px;
	font-size: 13px;
	line-height: 1.4;
	color: #aaa;
	margin: 20px 0;
	
}

.bg {
	display: inline-block;
	color: #fff;
	background: cornflowerblue;
	padding: 5px 10px;
	border-radius: 50px;
	font-size: .7em;
}
.button {
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	margin-top: 10px;
	
	
	
}

.button a {
	display: inline-block;
	overflow: hidden;
	position: relative;
	font-size: 11px;
	color: #111;
	text-decoration: none;
	padding: 10px 15px;
	border: 1px solid #aaa;
	font-weight: bold;
	
	
}

.button a:after{
	content: "";
	position: absolute;
	top: 0;
	right: -10px;
	width: 0%;
	background: #111;
	height: 100%;
	z-index: -1;
	transition: width 0.3s ease-in-out;
	-webkit-transform: skew(-25deg);
	        transform: skew(-25deg);
	-webkit-transform-origin: right;
	        transform-origin: right;
}

.button a:hover:after {
	width: 150%;
	left: -10px;
	-webkit-transform-origin: left;
	        transform-origin: left;
	
}

.button a:hover {
	color: #fff;
	transition: all 0.5s ease;
}


.button a:nth-of-type(1) {
	border-radius: 50px 0 0 50px;
	border-right: none;
}

.button a:nth-of-type(2) {
	border-radius: 0px 50px 50px 0;
}

.cart-icon {
	padding-right: 8px;
	
}

.footer {
	position: absolute;
	bottom: 0;
	right: 0;
}
